import React, { useEffect, useState, useRef } from "react";
import { addMessage, loadChat } from "../../utils/chatStorage";
import { Paperclip, Send } from "lucide-react";

export default function BuyerChatPage({ buyerId }) {
  const [messages, setMessages] = useState([]);
  const [text, setText] = useState("");
  const [attachments, setAttachments] = useState([]);
  const fileInputRef = useRef(null);
  const messagesEndRef = useRef(null);

  useEffect(() => {
    const interval = setInterval(() => {
      const data = loadChat();
      setMessages(data.messages.filter(m => m.buyerId === buyerId));
      messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }, 500);
    return () => clearInterval(interval);
  }, [buyerId]);

  const sendMessage = () => {
    if (!text && attachments.length === 0) return;
    addMessage("buyer", text, attachments, buyerId);
    setText("");
    setAttachments([]);
  };

  const handleFiles = (files) => {
    const newAtts = Array.from(files).map(file => {
      const type = file.type.startsWith("image/") ? "image" :
                   file.type.startsWith("video/") ? "video" : "file";
      const preview = type === "file" ? null : URL.createObjectURL(file);
      return { id: Date.now() + Math.random(), file, type, preview };
    });
    setAttachments(prev => [...prev, ...newAtts]);
  };

  return (
    <div className="min-h-screen bg-gray-50 p-4 max-w-xl mx-auto">
      <h1 className="text-xl font-bold mb-4">Buyer Chat ({buyerId})</h1>

      <div className="bg-white p-3 rounded-md shadow-md flex flex-col gap-2 max-h-[60vh] overflow-auto">
        {messages.length === 0 && <p className="text-gray-400 text-center">No messages yet</p>}
        {messages.map(m => (
          <div key={m.id} className={`p-2 rounded-md ${m.from==="buyer"?"bg-blue-600 text-white self-end":"bg-gray-200 self-start"}`}>
            {m.text && <div>{m.text}</div>}
            {m.attachments?.map(a => (
              a.type==="image"?<img key={a.id} src={a.preview} className="w-24 h-24 object-cover mt-1"/>:
              a.type==="video"?<video key={a.id} src={a.preview} className="w-24 h-24 mt-1" controls/>:
              <div key={a.id} className="bg-gray-100 p-1 rounded mt-1">{a.file.name}</div>
            ))}
          </div>
        ))}
        <div ref={messagesEndRef}/>
      </div>

      <div className="mt-2 flex gap-2">
        <button onClick={()=>fileInputRef.current.click()} className="p-2 bg-gray-200 rounded-md"><Paperclip/></button>
        <input type="file" ref={fileInputRef} className="hidden" multiple onChange={e=>handleFiles(e.target.files)}/>
        <textarea value={text} onChange={e=>setText(e.target.value)} className="flex-1 border rounded-md p-2 resize-none" placeholder="Write a message..."/>
        <button onClick={sendMessage} className="bg-green-600 text-white p-2 rounded-md flex items-center gap-1"><Send/> Send</button>
      </div>
    </div>
  );
}
